.gpt3__features{
    display: flex;
    flex-direction: row;
    width: 100%;
     justify-content: space-between;
}

.gpt3__features-heading{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-content: flex-start;
     margin-right: 5rem;
     text-align: left;
 
  }
.gpt3__features-heading h1{
    font-family: var(--font-family);
font-weight: 800;
font-size: 30px;
line-height: 40px;
/* or 132% */

}

.gpt3__features-heading p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    padding-top: 30px;
    color: #FF8A71;
    
}
.gpt3__features-container{
    flex: 1.2;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

@media screen and (max-width: 990px) {
    .gpt3__features{
        flex-direction: column;
    }
    .gpt3__features-heading{
        margin: 0 0 2rem 0;
        }
}


@media screen and (max-width: 990px) {
.gpt3__features-heading h1{
    font-size: 28px;
    line-height: 35px;
    }
}